import { Component } from '@angular/core';
import { NavController, AlertController } from 'ionic-angular';
import { ResultPage } from '../result/result';
declare var echarts;

@Component({
  selector: 'page-bj',
  templateUrl: 'bj.html'
})
export class BJPage {

  gridArray = [
    [
      {
        t1: "自",
        t2: "由",
        selected: false
      },
      {
        t1: "爱",
        t2: "国",
        selected: false
      },
      {
        t1: "富",
        t2: "强",
        selected: false
      }
    ],
    [
      {
        t1: "平",
        t2: "等",
        selected: false
      },
      {
        t1: "诚",
        t2: "信",
        selected: false
      },
      {
        t1: "民",
        t2: "主",
        selected: false
      }
    ],
    [
      {
        t1: "公",
        t2: "正",
        selected: false
      },
      {
        t1: "敬",
        t2: "业",
        selected: false
      },
      {
        t1: "文",
        t2: "明",
        selected: false
      }
    ],
    [
      {
        t1: "法",
        t2: "治",
        selected: false
      },
      {
        t1: "友",
        t2: "善",
        selected: false
      },
      {
        t1: "和",
        t2: "谐",
        selected: false
      }
    ]
  ]

  selected: Array<number> = [];

  constructor(
    public navCtrl: NavController,
    private alertCtrl: AlertController) {

  }
  ionViewDidLoad() {
    // this.playMusic();
    this.initEcharts6();
    this.initEcharts10();
  }

  listen() {
    //--创建页面监听，等待微信端页面加载完毕 触发音频播放
    document.addEventListener('DOMContentLoaded', () => {
      let audio: HTMLAudioElement = <HTMLAudioElement>document.getElementById("bj_audio_id");
      audio.play();
    });
    document.addEventListener("WeixinJSBridgeReady", () => {
      let audio: HTMLAudioElement = <HTMLAudioElement>document.getElementById("bj_audio_id");
      audio.play();
    }, false);

    //--创建触摸监听，当浏览器打开页面时，触摸屏幕触发事件，进行音频播放
    document.addEventListener('touchstart', () => {
      let audio: HTMLAudioElement = <HTMLAudioElement>document.getElementById("bj_audio_id");
      audio.play();
    });
  }

  playMusic() {
    let url = "../assets/images/bg_music.mp3";
    var audio = new Audio(url);
    audio.loop = true;
    audio.autoplay = true;
    audio.play();

    //--创建页面监听，等待微信端页面加载完毕 触发音频播放
    document.addEventListener('DOMContentLoaded', () => {
      audio.play();
    });
    document.addEventListener("WeixinJSBridgeReady", () => {
      audio.play();
    }, false);

    //--创建触摸监听，当浏览器打开页面时，触摸屏幕触发事件，进行音频播放
    document.addEventListener('touchstart', () => {
      audio.play();
    });
  }

  initEcharts6() {
    echarts.init(document.getElementById('echarts_6')).setOption({
      legend: {
        // type: "scroll",
        top: "bottom",
        // orient:"vertical"
      },
      tooltip: {
        show: false
      },
      toolbox: {
        show: false
      },
      series: {
        type: 'pie',
        name: "中国皇帝寿命图",
        data: [
          {
            name: '31-40岁',
            value: 60,
            itemStyle: {
              color: '#33688e'
            }
          },
          {
            name: '21-30岁',
            value: 55,
            itemStyle: {
              color: '#357799'
            }
          },
          {
            name: '51-60岁',
            value: 54,
            itemStyle: {
              color: '#3a86a5'
            }
          },
          {
            name: '41-50岁',
            value: 52,
            itemStyle: {
              color: '#4096af'
            }
          },
          {
            name: '11-20岁',
            value: 34,
            itemStyle: {
              color: '#4ea4b9'
            }
          },
          {
            name: '61-70岁',
            value: 32,
            itemStyle: {
              color: '#67b5bf'
            }
          },
          {
            name: '1-10岁',
            value: 13,
            itemStyle: {
              color: '#80c1c9'
            }
          },
          {
            name: '71岁及以上',
            value: 10,
            itemStyle: {
              color: '#96cece'
            }
          },
        ],
        label: {
          show: true,
          // position:"inside",
          // align:"left",
          // verticalAlign:"top",
          formatter: "{c}",
        },
        labelLine: {
          show: true,
          length: 3,
          // length2:3,
          smooth: true
        }
      }
    });
  }

  initEcharts10() {
    echarts.init(document.getElementById('echarts_10')).setOption({
      legend: {
        // type: "scroll",
        top: "bottom",
        // orient:"vertical"
      },
      tooltip: {
        show: false
      },
      toolbox: {
        show: false
      },
      series: {
        type: 'pie',
        name: "中国皇帝皇位获取途径图",
        data: [
          {
            name: '皇位继承',
            value: 196,
            itemStyle: {
              color: '#2F5A85'
            }
          },
          {
            name: '帝国建立',
            value: 39,
            itemStyle: {
              color: '#386D94'
            }
          },
          {
            name: '群臣拥立',
            value: 36,
            itemStyle: {
              color: '#377FA0'
            }
          },
          {
            name: '权臣拥立',
            value: 31,
            itemStyle: {
              color: '#3A8EB0'
            }
          },
          {
            name: '篡位',
            value: 25,
            itemStyle: {
              color: '#489EB7'
            }
          },
          {
            name: '皇位争夺',
            value: 16,
            itemStyle: {
              color: '#5EACBE'
            }
          },
          {
            name: '自立为帝',
            value: 10,
            itemStyle: {
              color: '#74BAC5'
            }
          },
          {
            name: '政变称帝',
            value: 5,
            itemStyle: {
              color: '#8DC7CE'
            }
          },
          {
            name: '册封',
            value: 2,
            itemStyle: {
              color: '#AFDAD5'
            }
          }
        ],
        label: {
          show: true,
          // position:"inside",
          // align:"left",
          // verticalAlign:"top",
          formatter: "{c}",
        },
        labelLine: {
          show: true,
          length: 3,
          // length2:3,
          smooth: true
        }
      }
    });
  }

  clickItem(row, col) {
    let num = row * 3 + col;
    if (this.selected.length == 0) {
      this.selected.push(num);
    } else if (this.selected.length == 1) {
      if (this.selected[0] == num) {
        this.selected = [];
      } else {
        this.selected.push(num);
      }
    } else if (this.selected.length == 2) {
      if (this.selected[0] == num) {
        this.selected.splice(0, 1);
      } else if (this.selected[1] == num) {
        this.selected.splice(1, 1);
      } else {
        this.presentAlert("只能选择2项,请去掉已选项再勾选");
        return;
      }
    }

    let cols = this.gridArray[row];
    let item = cols[col];
    item.selected = !item.selected;
  }

  presentAlert(subTitle) {
    let alert = this.alertCtrl.create({
      title: "提示",
      subTitle: subTitle,
      buttons: ['知道了']
    });
    alert.present();
  }

  clickButton() {
    if (this.selected.length == 2) {
      this.navCtrl.push(ResultPage, this.selected);
    } else {
      this.presentAlert("请选满2项后查看");
    }
  }

}



