import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';

@Component({
  selector: 'page-result',
  templateUrl: 'result.html',
})
export class ResultPage {

  selected: Array<number> = [];

  src: string;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams) {
  }

  ionViewDidLoad() {
    this.selected = this.navParams.data;

    this.src = this.getImage();
  }

  getImage() {
    let num1 = this.selected[0];
    let num2 = this.selected[1];

    let b1 = 1 << num1;
    let b2 = 1 << num2;
    let b = b1 ^ b2;
    let str = b.toString(2);
    console.log("str:",str);
    console.log("join:",Array(12-str.length+1).join('0'));
    let str_12 = Array(12-str.length+1).join('0') + str;
    return "assets/images/result/" + str_12 + ".jpg";
  }
}



